import React, { useEffect } from "react"
import { useState } from "react"
import { useNavigate } from 'react-router-dom';

import "../../assets/css/pages/map.css"
import KeplerMap from "./KeplerMap"
import { useParams } from 'react-router-dom';

export default function Map() {
  const [isMenuActive, toggleMenuActiveMode] = useState(false)
  const [activeMapId, setActiveMapId] = useState(1)
  const navigate = useNavigate()

  const maps = [
    { id: 1, name: 'Traffic Map', keyword: 'traffic-map' },
    { id: 2, name: 'Cluster Map', keyword: 'cluster-map' },
    { id: 3, name: 'Czech Highway Weekly Traffic', keyword: 'czech-highway-weekly-traffic' },
  ]

  const { type = 'traffic-map' } = useParams()

  useEffect(() => {
    // Обработчик изменения параметра запроса
    const handleQueryParamChange = () => {

      const map = maps.find(map => map.keyword === type)

      const id = map ? map.id : 1

      setActiveMapId(id)
    };

    // Вызов обработчика при монтировании компонента
    handleQueryParamChange()
  }, [])


  function changeMap(map) {
    const msg = `Do you want to change the map to ${map.name}?`

    if (window.confirm(msg)) {
      navigate(`/map/${map.keyword}`, { replace: true })

      setActiveMapId(map.id)
      toggleMenuActiveMode(false)
    }
  }

  return (
    <main className="map-page">
      <nav className="map-page-navigation">
        <div className="dropdown open">
          <button onClick={() => toggleMenuActiveMode(!isMenuActive)} className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            Maps list
          </button>

          {
            isMenuActive ? (
              <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                {
                  maps.map((map, index) => {
                    return (
                      <div
                        key={index}
                        onClick={() => changeMap(map)}
                        className="dropdown-item">
                        {
                          map.id === activeMapId
                            ? <span className="active">–</span>
                            : ''
                        }

                        {map.name}
                      </div>
                    )
                  })
                }
              </div>
            ) : ('')
          }
        </div>
      </nav>

      <div className="map-area">
        <KeplerMap id={activeMapId} />
      </div>
    </main>
  )
}